li {
  cursor: pointer;
  text-decoration: none;
  list-style: none;
  text-align: center;
  width: 100%;
  color: white;
  transition: all 0.5s;
}

ul {
  width: 100%;
  align-self: center;
  justify-self: center;
  padding: 0;
  line-height: 60px;
  font-family: "Segoe UI";
  font-size: 18px;
}

li:hover {
  background-color: black;
  color: white;
}

.sidebar {
  height: 100%;
  border-right: 1.5px solid white;
  width: 20%;
  min-width: min-content;
  position: fixed;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.4);
  margin: 0;
  transition: all 5s;
}
