.Paginas {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.siteImg {
  height: 120px;
  border: 1px solid white;
  width: calc(1.77*120px);
}

.scrollView {
  border-radius: 5px;
  overflow-x: scroll;
  overflow-y: hidden;
  display: flex;
  width: 95%;
  height: 200px;
  margin-left: 2.5%;
  align-self: center;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.cardContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between ;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 15px;
  margin: 10px 5px 10px 5px;
}

.cardContainer:hover {
  border: 1px solid white;
  box-shadow: 0px 0px 10px 2px white;
}

.itemTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

#paginasTitle {
  width: 100%;
  height: 30%;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#paginasContent {
  border-top: 1.5px solid white;
  width: 100%;
  height: 70%;
  text-align: center;
  justify-content: flex-start;
  background-color: rgba(0, 0, 0, 0.4);
  overflow-y: auto;
}

span {
  margin-top: 5px;
  color: white;
}

span:hover, a:hover {
  text-decoration: none;
}

h3 {
  margin: 5px;
  padding: 5px;
}

h5 {
  margin: 10px;
}

a {
  padding: 0;
}

p {
  width: 100%;
  padding: 10px;
}
